import { useState } from "react";
import AddTodo from "./AddTodo";
import TodoItem from "./TodoItem";
import "./Todo.css";
let Todo = () => {
  const [todos, setTodo] = useState([]);

  const handleDelete = (id) => {
    setTodo(todos.filter((element) => element.id !== id));
  };

  const handleToggle = (id) => {
    const updatedTodo = todos.map((element) =>
      element.id === id ? { ...element, status: !element.status } : element
    );
    setTodo(updatedTodo);
  };

  const handleAddTodo = (text) => {
    const userItem = {
      title: text,
      status: false,
      id: new Date().toDateString() + text
    };
    console.log(userItem);
    setTodo([...todos, userItem]);
    console.log(todos);
  };
  return (
    <div className="main">
      <AddTodo data={todos} handleAddTodo={handleAddTodo} />
      <ul className="list-container">
        {todos.map((item) => (
          <TodoItem
            key={item.id}
            title={item.title}
            status={item.status}
            handleDelete={() => handleDelete(item.id)}
            handleToggle={() => handleToggle(item.id)}
          />
        ))}
      </ul>
    </div>
  );
};
export default Todo;
